/*bigger screen screen*/
@media (min-width: 956px) {
  .project_projectContainer__3PS28 {
    width: 60%;
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
    align-items: center;
  }
  .project_projectTitle__iv6i0 {
    margin-bottom: 20px;
    text-align: center;
    font-size: 15px;
    font-family: "Source Code Pro", monospace;
  }
  /*this is for image component*/
  .project_projectContent__fzag5 {
    display: flex;
    margin-bottom: 40px;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    align-items: flex-start;
    justify-content: center;
  }
  .project_projectName__3EqLn {
    position: absolute;
    font-family: "Source Code Pro", monospace;
    z-index: 1;
    transform: rotate(-270deg);
    transform-origin: top left;
    left: 10%;
    top: 0%;
    box-sizing: border-box;
    font-weight: bold;
  }

  .project_projectImage__SdgcR {
    box-sizing: border-box;
    border: 0.5px solid gray;
    width: auto;
    padding: 1px;
    height: 50vh;
  }
}

